@import url(https://fonts.googleapis.com/css2?family=Viga&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap);
body {
  /* margin: 0; */
  height: 100%;
  background-image: url(/help-button/static/media/bg.de8bb2ae.jpg);
  font-family: 'Viga', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
  --langbar-width: 180px;
}

.bl_lang {
  position: absolute;
  top: 1%;
  width: 180px;
  width: var(--langbar-width);
  left: 50%;
  margin-left: calc(-1 * 180px * 0.5);
  margin-left: calc(-1 * var(--langbar-width) * 0.5);
}

.lang {
  padding-bottom: 10px;
  color: #b2b2b2;
}

.country span {
  margin: 5px;
}
.inactive {
  opacity: 0.14;
  filter: alpha(opacity=14);
}

@media only screen and (max-width: 680px) {
  .bl_lang {
    position: relative;
    margin-top: 24px;
  }
}

@media only screen and (max-height: 750px) {
  .bl_lang {
    position: relative;
    margin-top: 24px;
  }
}

:root {
  --header-width: 610px;
}

.header {
  position: absolute;
  top: 18%;
  left: 50%;
  margin-left: calc(-1 * 610px * 0.5);
  margin-left: calc(-1 * var(--header-width) * 0.5);
}

.header h1 {
  font-size: 45px;
  text-transform: uppercase;
  width: 610px;
  width: var(--header-width);
  margin: 0;
  padding-bottom: 6px;
  color: #6e6e6e;
  background: url(data:image/png;base64,) no-repeat left bottom;
}

.header p {
  margin: 0;
  font-family: 'Nunito', sans-serif;
}

@media only screen and (max-width: 680px) {
  .header {
    width: auto;
    position: relative;
    margin-left: 0;
    left: 0;
  }
  .header h1 {
    font-size: 45px;
    width: auto;
    background: none;
  }
}

@media only screen and (max-width: 550px) {
  .header h1 {
    font-size: 40px;
  }
}

@media only screen and (max-height: 750px) {
  .header {
    width: auto;
    position: relative;
    margin-left: 0;
    left: 0;
  }
  .header h1 {
    font-size: 45px;
    width: auto;
    padding-bottom: 0px;
    background: none;
  }
}

.copyright {
  color: #b2b2b2;
  position: absolute;
  width: 180px;
  margin-left: -90px;
  left: 50%;
  top: 94%;
}

@media only screen and (max-width: 680px) {
  .copyright {
    position: relative;
    width: auto;
    margin-left: 0px;
    left: 0%;
    top: 0%;
    margin: 30px 0 20px 0;
  }
}

@media only screen and (max-height: 750px) {
  .copyright {
    position: relative;
    width: auto;
    margin-left: 0px;
    left: 0%;
    top: 0%;
    margin: 30px 0 20px 0;
  }
}

.bt {
  position: absolute;
  height: 287px;
  width: 287px;
  left: 50%;
  margin-left: -144px;
}

.bt {
  position: absolute;
  height: 287px;
  width: 287px;
  left: 50%;
  margin-left: -144px;
}
.btn-help {
  font-size: 70px;
  color: #fff;
  text-transform: uppercase;
  background: url(/help-button/static/media/btn.4d8a5e90.png) no-repeat center;
  height: 287px;
  width: 287px;
  line-height: 287px;
  position: absolute;
  z-index: 32px;
  margin-top: 7px;
}
.btn-help:active {
  margin-top: 14px;
}
.bt-fon {
  height: 287px;
  width: 287px;
  position: absolute;
  z-index: 1px;
  background: url(data:image/png;base64,) no-repeat center;
  margin-top: 7px;
}

:root {
  --submit-btn-height: 58px;
  --back-btn-height: 35px;
  --press-btn-distance: 7px;
}
.form {
  text-transform: uppercase;
}
input {
  height: 74px;
  line-height: 74px;
  margin-bottom: 15px;
  border: none;
  width: 80%;
  padding: 0 10%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.14);
  font-family: 'Nunito', sans-serif;
  font-size: 19px;
  display: none;
  border: 1px solid #fff;
}
textarea {
  max-width: 440px;
  height: 118px;
  resize: none;
  border: none;
  width: 80%;
  padding: 30px 10%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.14);
  font-size: 19px;
  font-family: 'Nunito', sans-serif;
  display: none;
  border: 1px solid #fff;
  margin-bottom: 15px;
}
.bt2 {
  height: 58px;
  height: var(--submit-btn-height);
  position: relative;
  display: none;
  padding: 0 1px;
}
.bt-fon2 {
  background-color: #b31212;
  width: 100%;
  height: calc(58px - 7px);
  height: calc(var(--submit-btn-height) - var(--press-btn-distance));
  margin-top: 7px;
  margin-top: var(--press-btn-distance);
  position: absolute;
}
.btn-popup {
  background-color: #dd1e1e;
  width: 100%;
  height: calc(58px - 7px);
  height: calc(var(--submit-btn-height) - var(--press-btn-distance));
  position: absolute;
  line-height: 58px;
  line-height: var(--submit-btn-height);
  color: #fff;
  font-size: calc(58px / 2);
  font-size: calc(var(--submit-btn-height) / 2);
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.64);
}
.btn-popup:active {
  margin-top: 7px;
  margin-top: var(--press-btn-distance);
}

.ugol5 {
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -khtml-border-radius: 5px;
}

.bt3 {
  height: 35px;
  height: var(--back-btn-height);
  position: relative;
  display: none;
  margin-bottom: 15px;
  margin-top: 15px;
  padding: 0 1px;
}

.bt-fon3 {
  background-color: rgba(0, 0, 0, 0.64);
  width: 100%;
  height: calc(35px - 7px);
  height: calc(var(--back-btn-height) - var(--press-btn-distance));
  margin-top: 7px;
  margin-top: var(--press-btn-distance);
  position: absolute;
}

.btn-popup3 {
  background-color: rgb(112, 112, 112);
  width: 100%;
  height: calc(35px - 7px);
  height: calc(var(--back-btn-height) - var(--press-btn-distance));
  position: absolute;
  line-height: 35px;
  line-height: var(--back-btn-height);
  color: #fff;
  font-size: calc(35px / 2.5);
  font-size: calc(var(--back-btn-height) / 2.5);
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.64);
}

.btn-popup3:active {
  margin-top: 7px;
  margin-top: var(--press-btn-distance);
}

@media only screen and (max-width: 550px) {
  .form {
    left: 0%;
    margin: 0px;
  }
}

.support {
  padding-top: 310px;
  color: #6e6e6e;
}
.support span {
  color: #6e6e6e;
  text-decoration: none;
  border-bottom: 1px dashed;
}

.block {
  position: absolute;
  max-width: 444px;
  width: 86%;
  left: 50%;
  top: 28%;
  margin-left: -222px;
}

.transition {
  transition: all 0.25s;
}

@media only screen and (max-width: 680px) {
  .block {
    position: relative;
    left: 0;
    margin: 0 auto;
  }
}

@media only screen and (max-height: 750px) {
  .block {
    height: none;
    position: relative;
    left: 0;
    margin: 0 auto;
  }
}

:root {
  --hover-height: 100%;
}
.scroll {
  display: none;
  position: fixed;
  /* background-color: crimson; */
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow-y: scroll;
  z-index: 999;
}

.hover {
  width: 100%;
  height: 150%;
  margin: 0;
  padding: 0;
  background-color: #000;
  opacity: 0.6;
  filter: alpha(opacity=60);
  cursor: pointer;
}

.popup-sent {
  display: none;
  max-width: 590px;
  width: 85%;

  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 20% auto;
  text-align: center;
  color: #2f2f2f;
}

.popup-sent img {
  margin: 60px 0 30px 0;
}
.popup-sent .title {
  font-size: 34px;
  /* font-family: MUSEOSANSCYRL_900; */
  text-transform: uppercase;
}
.popup-sent .desc {
  font-size: 23px;
  margin: 20px 0 60px 0;
  text-transform: uppercase;
}

.popup-support {
  display: none;
  max-width: 590px;
  width: 85%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 20% auto;
  text-align: center;
  color: #2f2f2f;
}
.popup-support .title {
  font-size: 20px;
  /* font-family: MUSEOSANSCYRL_900; */
  text-transform: uppercase;
  padding-top: 40px;
}
.popup-support .desc {
  font-size: 18px;
  margin: 15px 20px;
}

.App {
  text-align: center;
}

.btn {
  -o-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all 0.3s;

  cursor: pointer;
}

